<template>
    <div id="merchandise" class="">
        <good-tree 
        class="good-tree" 
        ref="mertree"
         @margin-change="changeMargin"></good-tree>

        <access-table
        ref="acctable" 
        v-if="type === GoodType.ACCESSORY" 
        class="good-table"
        :draftNum="draftNum"></access-table>

        <good-table 
        v-else 
        class="good-table" 
        :draftNum="draftNum" 
        ref="goodtable" ></good-table>
    </div>
</template>

<script>
import GoodTree from "./goodTree";
import GoodTable from "./goodTable";
import AccessTable from "./accessTable";

import { GoodType } from "@/config/cfg";
import { getTableList } from "../api";

export default {
  components: {
    GoodTree,
    GoodTable,
    AccessTable
  },
  data() {
    return {
      GoodType: GoodType, //菜品类型cfg
      draftNum: 0
    };
  },
  computed: {
    type() {
      return this.$store.state.good.type;
    }
  },
  watch: {},
  created() {
    this.getDraftNum();
  },

  methods: {
    //获取草稿数量
    getDraftNum() {
      let data = {
        foodlist: 1,
        is_draft: 1
      };

      getTableList(data, resp => {
        if (resp.ret === 0) {
          this.draftNum = resp.data.total;
        }
      });
    },
    changeMargin() {
      setTimeout(() => {
        let x = this.$refs.mertree.$el.clientWidth;
        if (x != 70) {
          if (this.$refs.goodtable) {
            this.$refs.goodtable.$el.style.marginLeft = x + 14 + "px";
          }
          if (this.$refs.acctable) {
            this.$refs.acctable.$el.style.marginLeft = x + 14 + "px";
          }
        }
      }, 20);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
@import "src/styles/flex.scss";

#merchandise {
  position: relative;
  @include wh(100%, 100%);
}

.good-tree {
  // width: 343px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;

  background-color: #fff;
  margin-right: 14px;
}

.good-table {
  margin-left: 357px;
  // width: 100%;
  background-color: #fff;
}
</style>


